.delivery-wrapper {
  width: 100%;
  background: url('#{$path}/light-tree.png') center center repeat;
  padding-bottom: 30px;
}

.delivery {
  margin: auto;
  max-width: $main-width;
}

.delivery__header {
  text-transform: uppercase;
  font-size: 2em;
  font-family: $font-os-condenced-bold;
  color: $color-green-dark;
  background: url('#{$path}/yellow-header.png') left center no-repeat;
  height: 100px;
  line-height: 100px;
  padding-left: 30px;
}

p.delivery__header {
  margin-bottom: 0px;
}

.delivery__header--margin {
  margin-left: 50px;
}

.delivery__subheader {
  color: $color-green-md-dark;
}

.delivery__button {
  margin-right: 70px;
  margin-top: 30px;
  display: inline-block;
  background: none;
  border: 2px solid #bcc29b;
  padding: 7px;
  font-size: 1.2em;
  color: $color-green-dark;
  text-transform: uppercase;
  @include border-radius(6px);
  text-decoration: none;
  font-family: $font-os-condenced-bold;
  padding: 5px 10px;
}

.delivery__desc {
  line-height: 1.2;
  font-size: 1.3em;
}

.container.grid-list-xs .layout .flex .delivery__desc {
  padding: 4px;
}

.delivery__tel {
  color: $color-green-dark;
  font-family: $font-os-condenced-bold;
  font-size: 1.1em;
}

.deliverty_tabl {
  font-size: 1.3em;
}



